import { v4 as uuid } from "uuid"
import { createHtmlElement } from "../utils"
import "@fortawesome/fontawesome-free/css/all.min.css" 

class TodoList{
    /**
     *      Bir TodoList instance' yaratr. 
     * Taraycnn "localStorage" ksmnda depolanan "todos" nesnesini eker.
     * Daha sonra listedeki yaplacaklar yaplm olarak iaretlememizi,
     * silmemizi veya listeye yeni bir yaplacak eklememizi salayan
     * "eventleri" oluturur.
     */
    constructor() {
        this.todos = this.getTodosFromLocalStorage()

        if (this.todos === null || typeof this.todos === "undefined" || this.todos.length <= 0) {
            this.todos = [
                {"id":uuid(), "todo": "DSC'ye uye ol", "isComplete": false},
                {"id":uuid(), "todo": "Etkinliklere katil", "isComplete": false},
            ]

            this.saveTodosToLocalStorage()
        }

        this.renderCurrentTodos()

        this.setEventListeners()
    }

    /**
     *      Yaplacaklar yani "todo" gelerinin sahip olduu HTML elementlerini seeer.
     *  Daha sonra bu elementlere bir "eventListener" ekler. Bu sayede kullanc 
     * bu elementler ile etkileime getiinde, "todo" hakknda yaplmas istenen
     * ilemi gerekletiren fonksiyonlar arlm olur.
     */
    setEventListeners() {
        const addTodoField = document.querySelector("#addTodo")
        const toggleTodoButtons = document.querySelectorAll(".toggleTodo")
        const removeTodoButtons = document.querySelectorAll(".removeTodo")
        const todoIcons = document.querySelectorAll("i")

        
        //Yeni bir todo yaratmak
        addTodoField.addEventListener("keypress", e => {
            if (e.keyCode === 13) {
                this.handleAddTodoClick(e.target.value);
                e.target.value = "";
            }
        })

        
        //Todolar tamamlanm/tamamlanmam olarak iaretlemek
        for (let i = 0; i < toggleTodoButtons.length; i++){
            toggleTodoButtons[i].addEventListener("click", e => {
                this.handleToggleTodoClick(e.target.id)
            })
        } 

        
        //Var olan bir todoyu silmek
        for (let i = 0; i < removeTodoButtons.length; i++){
            removeTodoButtons[i].addEventListener("click", e => {
                this.handleRemoveTodoClick(e.target.id)
            })
        }

        //konlar hem todo iaretlemede hem de todo silmede kullanlabilir
        for (let i = 0; i < todoIcons.length; i++){
            todoIcons[i].addEventListener("click", e => {
                if (e.target.parentElement.classList.contains("toggleTodo")) {
                this.handleToggleTodoClick(e.target.parentElement.id)
                }
                if (e.target.parentElement.classList.contains("removeTodo")) {
                    this.handleRemoveTodoClick(e.target.parentElement.id)
                }
            })
        }
    }

    /**
     * Kullanc yeni todo ekleme butonuna bastnda bu fonksiyon alr.
     * @param {{id:String, todo:String, isComplete:boolean}} todo 
     */
    handleAddTodoClick(todo) {
        const newTodo = {
            "id": uuid(),
            "todo": todo,
            "isComplete":false
        }

        //Eer todo ierii bo ise, kullancya bir hata dndr.
        if (todo === "") {
            return null
        } else {
            //Todo ierii bo deil ise bu todoyu kaydet.
            return this.addTodo(newTodo)
        }
    }

    /**
     *  Yeni bir "todo" objesini hem "TodoList" instance'na hem de
     * taraycnn localStorage'na kaydetmeye yarar
     * @param {{id:String, todo:String, isComplete:boolean}} todo 
     */
    addTodo(todo) {
        this.todos.push(todo)
        this.saveTodosToLocalStorage()
        this.renderCurrentTodos()
        this.setEventListeners()
    }

    getTodosFromLocalStorage() {
        try {
            return JSON.parse(localStorage.getItem("todos"))
        } catch (error) {
            console.log(error)
            return null
        }
        
    }
    /**
     *  "TodoList" instance'nn sahip olduu todolar, taraycnn
     * localStorage'na kaydeder.
     */
    saveTodosToLocalStorage() {
        localStorage.setItem("todos", JSON.stringify(this.todos))
    }

    /**
     *   Kullancnn sahip olduu todolar ve bu toddolar hakknda ilem
     * yaplmasn salayan butonlar HTML'e render eder.
     */
    renderCurrentTodos() {
        const todoContainer = document.querySelector("#todo-container")
        todoContainer.innerHTML = ""
        this.todos.forEach(todo => {
            const todoDiv = createHtmlElement("div", "", {
                class:"todo-container"
            })
            //Todo metni
            const todoText = createHtmlElement("div", todo.todo, { class: "todo-text" })
            if (todo.isComplete) {
                todoText.classList.add("todo-crossed")
            }

            //Metni CSS ile sol tarafa dayamak iin
            todoText.classList.add("left")
            
            todoDiv.appendChild(todoText)

            //  Butonlarn girecei konteynr, CSS ile stil vermek
            //iin right class eklendi
            const todoButtonContainer = createHtmlElement(
                "div",
                "",
                {class:"right"}
            )
            //Todo'yu tamamlanm/tamamlanmam olarak iaretleme
            if (todo.isComplete) {
                const toggleAsCompletedButton = createHtmlElement(
                    "button",
                    "<i class='far fa-check-square'></i>",
                    {
                        id      :todo.id,
                        class   :"toggleTodo"
                    }
                )

                todoButtonContainer.appendChild(toggleAsCompletedButton)
            } else {
                const toggleAsUncompletedButton = createHtmlElement(
                    "button",
                    "<i class='far fa-square'></i>",
                    {
                        id      :todo.id,
                        class   :"toggleTodo"
                    }
                )

                todoButtonContainer.appendChild(toggleAsUncompletedButton)
            }

            //Todo'yu silme
            const removeTodoButton = createHtmlElement(
                "button",
                "<i class='far fa-trash-alt'></i>",
                {
                    class   : "removeTodo",
                    id      : todo.id
                }
            )

            todoButtonContainer.appendChild(removeTodoButton)

            todoDiv.appendChild(todoButtonContainer)

            const todoListElement = createHtmlElement("li", "", { class: "todo" })
            todoListElement.appendChild(todoDiv)
            todoContainer.appendChild(todoListElement)
        })
    }

    /**
     * Kullanc todo silme butonuna bastnda bu fonskiyon alr.
     * @param {String} todoId 
     */
    handleRemoveTodoClick(todoId) {
        this.todos = this.todos.filter(todo => todo.id !== todoId)
        this.saveTodosToLocalStorage()
        this.renderCurrentTodos()
        return this.setEventListeners()
    }

    /**
     *  Kullanc bir todo'ya ait tamamlanma durumunun deitirilmesini salayan
     * butona bastnda bu fonksiyon alr.
     * @param {String} todoId 
     */
    handleToggleTodoClick(todoId) {
        this.todos = this.todos.map(todo => 
            todo.id === todoId ? {...todo, isComplete: !todo.isComplete} : todo    
        )

        this.saveTodosToLocalStorage()
            
        this.renderCurrentTodos()

        return this.setEventListeners()
    }   
}

export default TodoList;